<template lang="html">
  <section class="todo">
    <div class="row">
      <div class="col-lg-12">
        <div class="card px-3">
          <div class="card-body">
            <h4 class="card-title">Todo list</h4>
            <form @submit.prevent="submitTodo">
              <div class="add-items d-flex">
                <input v-model="newTodo" type="text" class="form-control todo-list-input" placeholder="What do you need to do today?">
                <button class="add btn btn-primary font-weight-bold todo-list-add-btn" id="add-task">Add</button>
              </div>
            </form>
            <div class="list-wrapper">
              <ul class="d-flex flex-column-reverse todo-list">
                <li v-for="todo in todos" :key="todo.id">
                  <div class="form-check">
                    <label class="form-check-label">
                    <input class="checkbox" type="checkbox">{{todo.title}}<i class="input-helper"></i></label>
                  </div>
                  <i @click.prevent="deleteTodo(todo)" class="remove mdi mdi-close-circle-outline"></i>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script lang="js">
export default {
  name: 'todo',
  data () {
    return {
      todos: [],
      newTodo: ''
    }
  },
  watch: {
    todos: {
      handler () {
        localStorage.todos = JSON.stringify(this.todos)
      },
      deep: true
    }
  },
  mounted () {
    if (localStorage.todos) {
      this.todos = JSON.parse(localStorage.todos)
    }
  },
  methods: {
    submitTodo () {
      this.todos.push({
        title: this.newTodo,
        done: false
      })
      this.newTodo = ''
    },
    deleteTodo (todo) {
      const todoIndex = this.todos.indexOf(todo)
      this.todos.splice(todoIndex, 1)
    }
  }
}
</script>

<style scoped lang="scss">
  .todo {}
</style>



